@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  background: #f5f5f5;
}

body,
input,
button {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background: #f55;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timer-container {
  width: 100%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  padding: 5px;
}

p {
  font-size: 60pt;
  font-weight: normal;
  font-family: Rajdhani, Arial, Helvetica, sans-serif;
  color: #f55;
}

